@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700&display=swap);

*{
  padding:0;
  margin:0;
  font-family: 'Montserrat', sans-serif;
  outline: none;
  font-size: 18px;
}

@media (max-width: 1380px) {
  *{font-size: 17px;}
}
@media (max-width: 1100px) {
  *{font-size: 16px;}
}

@media (max-width: 1000px) {
  *{font-size: 15px;}
}
@media (max-width: 825px) {
  *{font-size: 14px;}
}

a:-webkit-any-link {
  text-decoration: none;
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
